<template>
  <div class="collection" style="width:100%">
    <div class="rg-top">
      <div class="rg-top-top">
        <span class="content-top-text-blue"></span>
        <span id="left_month">{{company[0].shenbao_month}}</span>
        <span style="margin-left: 2px">全年一次性收入</span>
        <span class="f-14 ml-50" style="font-weight: normal">申报总人数：</span>
        <span id="top_sum">{{ payYearTable.length }}</span>
        <span class="f-14 mr-20" style="font-weight: normal">人</span>
        <span class="f-14" style="font-weight: normal">收入总额：</span>
        <span id="top_add">{{ incomeAll }}</span>
        <span class="f-14 mr-20" style="font-weight: normal">元</span>
        <span class="f-14" style="font-weight: normal">免税收入合计：</span>
        <span id="top_del">{{ mianTaxAll }}</span>
        <span class="f-14" style="font-weight: normal">元</span>
      </div>
      <div class="rg-top-bottom">
        <div>
          <el-button type="primary" size="small" @click="goBack">返回</el-button>
          <el-button type="primary" size="small" @click="addFormClick">添加</el-button>
          <el-dropdown>
            <el-button type="primary" size="small" :split-button="true">
              导入
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">导入文件</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">模板下载</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div>
          <el-dropdown>
            <el-button type="text" size="small" class="right-btn">
              导出
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">导出当页</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">导出全部</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button type="text" size="small" class="right-btn" @click="changeSelectClick">展开查询条件</el-button>
          <el-dropdown>
            <el-button type="text" size="small" class="right-btn">
              更多操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="updataClick(formId)">修改</el-dropdown-item>
              <el-dropdown-item @click.native="deleteClick(formId)">删除</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">批量修改</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">自定义显示列</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">隐藏非正常人员</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">异常身份证处理</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <topSelect v-if="isSelect" :formId="formId" @selectClick="selectClick"></topSelect>
    <el-table
      :data="payYearTable"
      border
      fit
      style="margin-top: 5px"
      width="100%"
      size="mini"
      :ref="formId+'Table'"
      @selection-change="selsChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="employmentNumber" label="工号"></el-table-column>
      <el-table-column prop="personName" label="姓名"></el-table-column>
      <el-table-column prop="cardType" label="证照类型"></el-table-column>
      <el-table-column prop="cardNumber" label="证照号码" show-overflow-tooltip></el-table-column>
      <el-table-column label="所得期间起" width="100px">
        <template>{{company[0].time_start}}</template>
      </el-table-column>
      <el-table-column label="所得期间止" width="100px">
        <template>{{company[0].time_end}}</template>
      </el-table-column>
      <el-table-column prop="allYearBonus" label="全年一次性奖金额"></el-table-column>
      <el-table-column prop="incomeNoTax" label="免税收入" width="120px"></el-table-column>
      <el-table-column prop="elseIncome" label="其他" width="120px"></el-table-column>
      <el-table-column prop="otherAll.zhunyukouchu" label="准予扣除的捐赠额" width="120px"></el-table-column>
      <el-table-column prop="otherAll.jianmianshuie" label="减免税额"></el-table-column>
      <el-table-column prop="otherAll.yikoujiaoshuie" label="已扣缴税额"></el-table-column>
      <el-table-column prop="otherAll.remark" label="备注"></el-table-column>
    </el-table>

    <!-- 新增和修改表的弹窗 -->
    <el-dialog
      class="in-table"
      title="全年一次性收入"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="close_modal"
      width="1000px"
      top="10vh"
    >
      <div class="in-table-content">
        <!-- 表单开始的地方 -->
        <el-form
          :status-icon="true"
          :model="yearForm"
          :rules="rules"
          :ref="formId"
          label-width="110px"
          class="demo-ruleForm"
          label-position="right"
        >
          <!-- 弹窗所有的表格信息 限定为600px高度-->
          <div style="height:600px;padding:0 30px 0 0px;overflow-y:auto">
            <!-- 基本信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">基础信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="工号" style="width:33%">
                  <el-select v-model="yearForm.personInfoId" placeholder="-工号-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '雇员'"
                      :key="i"
                      :label="item.employmentNumber?item.employmentNumber:'暂未填写'"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证照类型" prop="cardType" style="width:50%">
                  <el-input value="身份证" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="所得时间起" prop="cardNumber" style="width:50%">
                  <el-input :value="company[0].time_start" readonly></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="姓名" style="width:33%" prop="personInfoId">
                  <el-select v-model="yearForm.personInfoId" placeholder="-请选择姓名-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '雇员'"
                      :key="i"
                      :label="item.personName"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证照号码" prop="cardType" style="width:50%">
                  <el-select v-model="yearForm.personInfoId" placeholder="请选择证件类型">
                    <el-option
                      v-for="(item, i) in personInfo"
                      v-if="item.employmentType === '雇员'"
                      :key="i"
                      :label="item.cardNumber"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="所得时间止" prop="cardNumber" style="width:50%">
                  <el-input :value="company[0].time_end" readonly></el-input>
                </el-form-item>
              </div>
            </div>
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">收入及减除费用</span>
              </div>
              <div style="display:flex">
                <el-form-item label="全年一次性奖金额" prop="cardType" style="width:50%">
                  <el-input type="number" v-model.number="yearForm.allYearBonus"></el-input>
                </el-form-item>
                <el-form-item label="免税收入" prop="cardType" style="width:50%">
                  <el-input type="number" v-model.number="yearForm.incomeNoTax"></el-input>
                </el-form-item>
              </div>
            </div>
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">本期其他扣除</span>
              </div>
              <div style="display:flex">
                <el-form-item label="其他" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="yearForm.elseIncome" @input="qitaInput"></el-input>
                </el-form-item>
                <el-form-item label="小计" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="yearForm.sum"></el-input>
                </el-form-item>
              </div>
            </div>

            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">本期其他</span>
              </div>
              <div style="display:flex">
                <el-form-item label="准予扣除的捐赠额" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="yearForm.otherAll.zhunyukouchu"></el-input>
                </el-form-item>
                <el-form-item label="减免税额" prop="cardNumber" style="width:33%">
                  <el-input type="number" v-model.number="yearForm.otherAll.jianmianshuie"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="已扣缴税额" prop="cardType" style="width:33%">
                  <el-input type="number" v-model.number="yearForm.otherAll.yikoujiaoshuie"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="备注" prop="cardType" style="width:100%">
                  <el-input v-model="yearForm.remark"></el-input>
                </el-form-item>
              </div>
            </div>
            <!-- 表格填写信息结束 -->
          </div>
          <el-form-item class="btn-bottom">
            <el-button type="primary" size="small" @click="submitForm(formId)">保存</el-button>
            <el-button size="small" @click="resetForm(formId)">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import formHelper from '@/until/formHelper'
import dataHelper from '@/until/dataHelper'
import commonData from '@/until/commonData'
import topSelect from './components/top-select'

var tableName = 'yearForm'
export default {
  components: {
    topSelect
  },
  data () {
    return {
      ...commonData,
      formId: tableName,
      close_modal: false,
      yearForm: {
        personInfoId: '',
        allYearBonus: 0,
        incomeNoTax: 0,
        elseIncome: 0,
        sum: 0,
        otherAll: {
          zhunyukouchu: 0,
          jianmianshuie: 0,
          jianchufeiyongbiaozhun: 0,
          yikoujiaoshuie: 0,
          remark: ''
        }
      },
      rules: {
        personInfoId: [
          { required: true, message: '请选择姓名' }
        ]
      },
      incomeAll: 0,
      mianTaxAll: 0
    }
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  computed: {
    ...mapGetters(['company']),
    personInfo: {
      get () {
        // console.log(dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId', 'employmentNumber'],{employmentType:'雇员'}))
        return dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId', 'employmentNumber', 'employmentType'], { employmentType: '雇员' })
      }
    },
    payYearTable: {
      get () {
        if (!this.isSelectNow) {
          let b = dataHelper.tableRelevanceArr(this, this.$store.state.form[tableName], this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
          return b
        } else {
          return this.selectArr
        }
      }
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.calculate()
  },
  mounted () {

  },
  methods: {
    calculate () {
      if (this.payYearTable.length > 0) {
        this.payYearTable.forEach(v => {
          this.incomeAll += v.income
          this.mianTaxAll += v.incomeNoTax
        })
      }
    },
    ...formHelper,
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          _this[formName].infoId = _this.$store.getters.getFormInfoId(formName) + 1
          _this[formName].incomeProject = '全年一次性奖金收入'
          let monthNumber = _this.$store.state.service.monthNumber
          // 累计收入
          _this[formName].incomeAll = _this[formName].allYearBonus
          // 本期收入
          _this[formName].income = _this[formName].allYearBonus
          // 累计免税收入
          _this[formName].incomeNoTaxAll = _this[formName].incomeNoTax
          // 累计减除费用
          _this[formName].otherAll.jianchufeiyongbiaozhunAll = 0
          _this[formName].elseIncome = parseFloat(_this[formName].elseIncome)
          // 累计扣除和减除项目合计 = 其他 + 累计准予扣除的捐赠额 + 减免税额
          _this[formName].jianchuAll = (parseFloat(_this[formName].elseIncome) + parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhunAll))
          // 累计准予扣除的捐赠额
          _this[formName].otherAll.zhunyukouchuAll = _this[formName].otherAll.zhunyukouchu
          // 累计应纳税额所得额计算 = 全年一次性奖金额 - 累计扣除项目合计
          _this[formName].leijiNashuiAll = parseFloat(_this[formName].incomeAll) - parseFloat(_this[formName].incomeNoTaxAll) - parseFloat(_this[formName].otherAll.jianchufeiyongbiaozhunAll) - parseFloat(_this[formName].jianchuAll) - parseFloat(_this[formName].otherAll.zhunyukouchuAll)
          // 累计减免税额
          _this[formName].otherAll.jianmianshuieAll = _this[formName].otherAll.jianmianshuie

          _this[formName].taxAll = dataHelper.yearShuilvPercent(_this[formName].leijiNashuiAll)
          // _this[formName].taxAll.taxPay = dataHelper.shuilvEnd(_this[formName].leijiNashuiAll, monthNumber)
          // 累计应扣缴税额 = 累计应纳税额 - 减免税额
          _this[formName].yingkoujiaoshuie = _this[formName].taxAll.taxPay - parseInt(_this[formName].otherAll.jianmianshuie)
          // 应补(退)税额 = 累计应纳税额 - 减免税额
          _this[formName].tuibu = _this[formName].yingkoujiaoshuie - _this[formName].otherAll.yikoujiaoshuie
          submitData.formId = formName
          submitData.content = _this[formName]
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }
          _this.centerDialogVisible = false
          this[formName] = this.$options.data()[formName]
          // alert('提交成功!')
          _this.$message.success('提交成功!')
          this.calculate()
        } else {
          return false
        }
      })
    },
    qitaInput () {
      this[tableName].sum = this[tableName].elseIncome
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  table {
    width: 10% !important;
  }

  .collection {
    font-size: 12px;
    text-align: start;
    margin-top: 10px;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }

  .mianshui-notice {
    font-size: 12px;
    padding: 1px 10px 20px 10px;
  }
</style>
<style>
  .el-form-item {
    margin-bottom: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



